import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { FormGroup } from '@angular/forms';

import { FormBase } from '../../../../class/form-base';
import { SettingService } from '../../../../services/setting/setting.service';
import { FormControlService } from '../../../../services/form-control/form-control.service';
import { MenuItem } from '../../../../class/menu-item';
import { WorkService } from '../../../../services/work/work.service';
import { OnsiteItem, OnsiteItemStatus, OnsiteStatus } from '../../../../class/onsite-item';

@Component({
  selector: 'app-page-work-detail',
  templateUrl: './page-work-detail.component.html',
  styleUrls: ['./page-work-detail.component.css']
})
export class PageWorkDetailComponent implements OnInit, OnDestroy {
  forms: FormBase<any>[];
  form: FormGroup;
  onsite: OnsiteItem;
  id = '';

  constructor(private settings: SettingService,
    private router: Router,
    private work: WorkService,
    private fcs: FormControlService,
    private route: ActivatedRoute) {
    const that = this;
    this.route.paramMap.subscribe((params: ParamMap) => {
      const id = parseInt(params.get('id'), 0);
      console.log(id);
      work.getOnsite(id, onsite => {
          that.onsite = onsite;
          that.forms = work.getWorkDetailForms(that.onsite.title);
          that.form = that.fcs.toFormGroup(that.forms);
      });
    });
  }

  ngOnInit() {
  }

  ngOnDestroy() {
  }

}



