import { Component, OnInit } from '@angular/core';
import { SmsNoticeService } from '../../../../../../services/sms-notice/sms-notice.service';
import { SmsNotice } from '../../../../../../class/menu-base';

@Component({
  selector: 'app-page-notice',
  templateUrl: './page-notice.component.html',
  styleUrls: ['./page-notice.component.css']
})
export class PageNoticeComponent implements OnInit {
  list: SmsNotice[] = [];

  constructor(private notice: SmsNoticeService ) {
    notice.getList(list => {
      this.list = list;
    });
  }

  ngOnInit() {
  }

}



