import { Component, OnInit, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { FormBase, Relation } from '../../../../class/form-base';
import { SettingService } from '../../../../services/setting/setting.service';
import { WorkService } from '../../../../services/work/work.service';

@Component({
  selector: 'app-work-form-child',
  templateUrl: './work-form-child.component.html',
  styleUrls: ['./work-form-child.component.css']
})

export class WorkFormChildComponent implements OnInit {
  @Input() workform: FormBase<any>;
  @Input() form: FormGroup;

  constructor(private settings: SettingService, private work: WorkService, private router: Router) {}

  ngOnInit() {
    // console.log(this.workform);
  }
  get isValid() { return this.form.controls[this.workform.key].valid; }

  onToUrl(url: string) {
    if (url !== '' || url !== undefined) {
      this.settings.setTitle(this.workform.label);
      this.router.navigate([this.workform['url']]);
    }
  }

  onShowBySrv() {
    this.work.onShowBySrv(this.workform , (element) => {
      this.workform['title'] = element.title;
      this.workform['content'] = element.content;
      this.workform.value = element;
    });
  }
}




