import { Component, ViewEncapsulation } from '@angular/core';
import { SettingService } from './services/setting/setting.service';
import { LoggerService } from './services/logger/logger.service';
import { DingtalkService } from './services/dingtalk/dingtalk.service';
// import * as $ from 'jquery';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent {
  title: String;
  isBack: Boolean;
  isWeb: Boolean;

  constructor(private dingtalk: DingtalkService,
    private settings: SettingService,
    private logger: LoggerService) {
    this.title = settings.getTitle();
    this.isWeb = (dd.version == null);
    console.log(dd);
    if (this.isWeb) {
      settings.title$.subscribe(title => {
        this.title = title;
        this.isBack = settings.isBack;
      });
    }else {
      if (dd.ios) {
        dd.ready(function() {
          dd.ui.webViewBounce.disable();
        });
      }
    }
    this.jQueryInit();
  }

  jQueryInit() {
    const that = this;
    const width = $(window).width();
    if (width > 320) {
      const pixel = (width / 750) * 22;
      $('html').css('font-size', pixel + 'px');
      // this.logger.log(pixel);
    }
    if (window.location.search.length > 0) {
      let corpid =  window.location.search;
      corpid = corpid.substring(1).split('&', 1)[0];
      corpid = corpid.split('=')[1];
      this.dingtalk.init(corpid, data => {
        // this.logger.log(data);
      });
    }
    jQuery(document).ready(function($) {
      if (window.history && window.history.pushState) {
        $(window).on('popstate', function() {
          const hashLocation = location.hash;
          const hashSplit = hashLocation.split('#!/');
          const hashName = hashSplit[1];
          if (hashName !== '') {
            const hash = window.location.hash;
            if (hash === '') {
              that.settings.back();
            }
          }
        });
        // window.history.pushState('forward', null, './#forward');
      }
    });
  }
}



