import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PageWorkComponent } from './page-work.component';
import { PageWorkListComponent } from './pages/page-work-list/page-work-list.component';
import { PageWorkDetailComponent } from './pages/page-work-detail/page-work-detail.component';
import { PageAddComponent } from './pages/page-add/page-add.component';
import { PageInventoryComponent } from './pages/page-add/pages/page-inventory/page-inventory.component';
import { PageAddWorkComponent } from './pages/page-add/pages/page-add-work/page-add-work.component';

const addRoutes: Routes = [
  { path: 'inventory', component: PageInventoryComponent },
  { path: ':type/:res', component: PageAddWorkComponent }
];


const detailRoutes: Routes = [
  { path: 'list/:type', component: PageWorkListComponent },
  { path: 'detail/:id', component: PageWorkDetailComponent }
];

const workRoutes: Routes = [
  { path: 'work', component: PageWorkComponent, children: [
    { path: 'add', component: PageAddComponent, children: addRoutes },
    { path: 'onsite', component: PageWorkComponent, children: detailRoutes },
    { path: 'design', component: PageWorkComponent, children: detailRoutes },
    { path: 'payment', component: PageWorkComponent, children: detailRoutes },
    { path: 'contract', component: PageWorkComponent, children: detailRoutes },
  ] }
];

@NgModule({
  imports: [
    RouterModule.forChild(workRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class WorkRoutingModule { }



