import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { NgZorroAntdModule } from 'ng-zorro-antd';

import { FormControlService } from './services/form-control/form-control.service';
import { DingtalkService } from './services/dingtalk/dingtalk.service';
import { SettingService } from './services/setting/setting.service';
import { LoggerService } from './services/logger/logger.service';
import { UserService } from './services/user/user.service';
import { WorkService } from './services/work/work.service';

import { HomeModule } from './pages/page-home/home.module';
import { UserModule } from './pages/page-user/user.module';
import { WorkModule } from './pages/page-work/work.module';

import { AppRoutingModule } from './app-routing.module';
import { HomeRoutingModule } from './pages/page-home/home-routing.module';
import { UserRoutingModule } from './pages/page-user/user-routing.module';
import { WorkRoutingModule } from './pages/page-work/work-routing.module';

import { AppComponent } from './app.component';
import { PageHomeComponent } from './pages/page-home/page-home.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { PageUserComponent } from './pages/page-user/page-user.component';
import { PageWorkComponent } from './pages/page-work/page-work.component';

import { NavBarComponent } from './components/nav-bar/nav-bar.component';
import { TabBarComponent } from './components/tab-bar/tab-bar.component';

@NgModule({
  declarations: [
    AppComponent,
    PageHomeComponent,
    PageNotFoundComponent,
    PageUserComponent,
    PageWorkComponent,
    TabBarComponent,
    NavBarComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    NgZorroAntdModule.forRoot(),
    HomeModule,
    UserModule,
    WorkModule,
    HomeRoutingModule,
    UserRoutingModule,
    WorkRoutingModule,
    AppRoutingModule
  ],
  providers: [
    FormControlService,
    DingtalkService,
    SettingService,
    LoggerService,
    UserService,
    WorkService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



