import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserService } from '../../../../services/user/user.service';
import { SettingService } from '../../../../services/setting/setting.service';
import { DingtalkService } from '../../../../services/dingtalk/dingtalk.service';
import { User } from '../../../../class/user';

@Component({
  selector: 'app-page-info',
  templateUrl: './page-info.component.html',
  styleUrls: ['./page-info.component.css']
})
export class PageInfoComponent implements OnInit, OnDestroy {
  user: User;
  dirty = false;
  isWeb = true;
  // userForm:

  constructor(private dingtalk: DingtalkService,
    private users: UserService,
    private settings: SettingService) {
    this.isWeb = dd.version === null;
    this.user = users.getUser();
    if (dd.version) {
      this.initDingtalk();
    }
  }

  ngOnInit() {
  }

  userFormChange(event) {
    this.dirty = event;
  }

  onSubmit() {
    if (dd.version) {
      const that = this;
      setTimeout(() => {
        that.dingtalk.hidePreloader(result => {
          that.dirty = false;
          that.dingtalk.toast('提交成功', 'success');
        });
      }, 1000);
    }
  }

  /**
   * 调用钉钉jsapi
   */
  initDingtalk() {
    const that = this;
    this.dingtalk.setNavigationRight('提交', true, true, result => {
      that.dingtalk.showPreloader('提交中...', true, res => {
        that.onSubmit();
      });
    });
    this.dingtalk.setNavigationLeft(res => {
      that.onBack();
    });
  }

  /**
   * 设置返回事件
   */
  onBack() {
    const that = this;
    if (this.dirty) {
      const message = '发现你已修改部分个人信息，确认要返回上一页吗？';
      this.dingtalk.setNavigationConfirm(message, result => {
        if (result.buttonIndex === 1) {
          that.dirty = false;
          that.onBack();
        }
      });
    }else {
      this.dingtalk.goBack();
    }
  }

  ngOnDestroy() {
    this.dingtalk.setNavigationRight();
  }

}



