import { NgModule } from '@angular/core';
import { PagesModule } from '../pages.module';
// import { PickerConfig } from 'ngx-weui';
import { ReactiveFormsModule } from '@angular/forms';
import { PageAddComponent } from './pages/page-add/page-add.component';
import { PageInventoryComponent } from './pages/page-add/pages/page-inventory/page-inventory.component';
import { PageAddWorkComponent } from './pages/page-add/pages/page-add-work/page-add-work.component';
import { PageWorkListComponent } from './pages/page-work-list/page-work-list.component';
import { PageWorkDetailComponent } from './pages/page-work-detail/page-work-detail.component';
import { WorkFormComponent } from './components/work-form/work-form.component';
import { WorkFormChildComponent } from './components/work-form-child/work-form-child.component';

@NgModule({
  imports: [
    PagesModule,
    ReactiveFormsModule
  ],
  declarations: [
    PageAddComponent,
    PageInventoryComponent,
    PageAddWorkComponent,
    PageWorkListComponent,
    PageWorkDetailComponent,
    WorkFormComponent,
    WorkFormChildComponent
  ]
})
export class WorkModule { }



