class MenuBase {
    title: string;
    icon: string;
    color: string;

    constructor(options: MenuBaseOpt = {}) {
        this.title = options.title || '';
        this.icon = options.icon || '';
        this.color = options.color || '#0099CC';
    }

    get mbClass () {
        return '#yjicon-' + this.icon;
    }

}

export class MenuTab extends MenuBase {
    url: string;
    constructor(options: MenuTabOpt = {}) {
        super(options);
        this.url = options.url || '';
    }
}

export class SmsNotice extends MenuBase {
    content: string;
    constructor(options: SmsNoticeOpt = {}) {
        super(options);
        this.content = options.content || '';
    }
}

export class WorkMenu extends MenuBase {
    list: WorkMenuItem[];
    constructor(options: WorkMenuOpt = {}) {
        super(options);
        this.list = options.list || [];
    }
    // constructor(
    //     public title: string,
    //     public icon: string = '',
    //     public color: string = '#0099CC',
    //     public list: WorkMenuItem[] = []) {}
}
export class WorkMenuItem extends MenuBase {
    url: string;
    addUrl: string;
    constructor(options: WorkMenuItemOpt = {}) {
        super(options);
        this.url = options.url || '';
        this.addUrl = options.addUrl || '';
    }
}
interface MenuBaseOpt {
    title?: string;
    icon?: string;
    color?: string;
}
interface MenuTabOpt extends MenuBaseOpt {
    url?: string;
}
interface SmsNoticeOpt extends MenuBaseOpt {
    content?: string;
}
interface WorkMenuOpt extends MenuBaseOpt {
    list?: WorkMenuItem[];
}
interface WorkMenuItemOpt extends MenuBaseOpt {
    url?: string;
    addUrl?: string;
}



