import { Injectable } from '@angular/core';
import { OnsiteItem, OnsiteStatus } from '../../class/onsite-item';
import { PickerData, PickerOptions, PickerService } from 'ngx-weui/picker';
import { FormBase,
  InputForm,
  TextareaForm,
  RelationForm,
  LinkForm,
  PhotosForm,
  LabelForm,
  AvatarsForm,
  ScheduleForm,
  Avatar,
  Relation,
  Schedule
} from '../../class/form-base';

@Injectable()
export class WorkService {
  private onsiteList: OnsiteItem[];
  contractTitles: Relation[];
  inventoryFormsValue: any[] = [];

  constructor(private srv: PickerService) {
    this.contractTitles = [
      new Relation(1, '武汉尚都花园总承包工程施工合同', '【武汉-尚都花园-一期】【总包】【1】【2017】'),
      new Relation(2, '深圳尚都花园总承包工程施工合同', '【深圳-尚都花园-一期】【总包】【1】【2017】'),
      new Relation(3, '上海尚都花园总承包工程施工合同', '【上海-尚都花园-一期】【总包】【1】【2017】'),
      new Relation(4, '广州尚都花园总承包工程施工合同', '【广州-尚都花园-一期】【总包】【1】【2017】'),
      new Relation(5, '北京尚都花园总承包工程施工合同', '【北京-尚都花园-一期】【总包】【1】【2017】'),
      new Relation(6, '长沙尚都花园总承包工程施工合同', '【长沙-尚都花园-一期】【总包】【1】【2017】'),
      new Relation(7, '厦门尚都花园总承包工程施工合同', '【厦门-尚都花园-一期】【总包】【1】【2017】'),
    ];
    this.onsiteList = [
      new OnsiteItem(0, 'https://modao.cc/uploads3/images/1275/12759365/raw_1505547729.jpeg',
        '桩基础工程35#楼1-50#桩冲孔过程中回填签证', OnsiteStatus.onsite, '武汉尚都花园总承包工程施工合同',
        new Date(2017, 8 , 16)),
      new OnsiteItem(1, 'https://modao.cc/uploads3/images/1275/12759572/raw_1505548205.jpeg',
        '三期小学土方运至红线外的签证申请', OnsiteStatus.draft, '武汉尚都花园总承包工程施工合同',
        new Date(2017, 8 , 2)),
      new OnsiteItem(2, 'https://modao.cc/uploads3/images/1275/12759590/raw_1505548242.jpeg',
        '项目YJ140T-6住宅父母房空调孔移位修改的签证申请', OnsiteStatus.internal, '武汉尚都花园总承包工程施工合同',
        new Date(2017, 7 , 20)),
      new OnsiteItem(3, 'https://modao.cc/uploads3/images/1275/12759608/raw_1505548276.jpeg',
        '三期小学土方运至红线外的签证申请', OnsiteStatus.success, '武汉尚都花园总承包工程施工合同',
        new Date(2017, 6 , 11)),
      new OnsiteItem(4, 'https://modao.cc/uploads3/images/1275/12759630/raw_1505548320.jpeg',
        '项目北环路、1#楼B前增加临时排水管道的签证申请', OnsiteStatus.success, '武汉尚都花园总承包工程施工合同',
        new Date(2016, 11 , 30))
    ];
    this.onsiteList = this.onsiteList.concat(this.onsiteList);
  }

  getOnsite(id: number, callback: Function) {
    callback(this.onsiteList[id]);
  }

  getOnsiteList(callback: Function) {
    callback(this.onsiteList);
  }

  getInventoryForms() {
    const inventoryForms: FormBase<any>[] = [
      new InputForm({key: 'division', label: '分部名称', required: false, placeholder: '非必填，100字内', order: 1}),
      new TextareaForm({key: 'suboption', label: '分项名称', required: true, placeholder: '必填，300字内', order: 2}),
      new TextareaForm({key: 'feature', label: '项目特征', required: false, placeholder: '必填，500字内', order: 3}),
      new InputForm({key: 'propose', label: '单位', required: true, placeholder: '必选', order: 4}),
      new InputForm({key: 'engineeringquantity', label: '工程量', required: true, placeholder: '必填，请输入数字', order: 5}),
      new InputForm({key: 'unitprice', label: '单价(元)', required: true, placeholder: '必填，请输入数字', order: 6}),
      new InputForm({key: 'totalprice', label: '总价(元)', required: true, placeholder: '必填，请输入数字', order: 7}),
      new TextareaForm({key: 'remark', label: '备注', required: false, placeholder: '非必填', order: 8})
    ];
    return inventoryForms;
  }

  getWorkDetailForms(title: string, ) {
    const content = '【武汉-尚都花园-一期】【总包】【1】【2017】-QZ-001';
    const detail = '施工部位 : ||\
                        1、武汉尚都花园项目北环路及1#楼B前 ||\
                    变更内容： ||\
                        1、武汉尚都花园项目北环路破除路面，并增加两条d300污水管（约104m）及2条d500雨水管（约104m）、1#楼B前增加4条d200污水管（约84m），变更平面图及大样详见附件。||\
                    原因详述： ||\
                        1、武汉尚都花园项目展示区即将开放，因市政北环路和市政雨污水排水管道暂未接通；||\
                        2、为将雨污水临时引向市政北环路（未施工）以北园区外，保证园区内排水，拟临时预留部分市政雨污水管道；||\
                        3、另为避免展示区1#楼B商铺前绿化面积水，拟增加4个雨水口并用D200波纹管引至临时路边池塘低洼处。||\
                    工期影响：无 ';

    const schedules: Schedule[] = [
      new Schedule('circlecheck', '#259b24', '#259b24', '莫桂富 （乙方现场工程师）', '发起审批', new Date(2017, 7, 2, 21, 47)),
      new Schedule('circlecheck', '#259b24', '#e51c23', '张同喜 （甲方工程师）  ', '打回到莫桂富-不重走流程',
        new Date(2017, 7, 3, 21, 47), '现场照片必须2张以上，工程量清单按电话沟通结果进行调整。'),
      new Schedule('circlecheck', '#259b24', '#259b24', '张同喜 （甲方工程师）  ', '重新发起审批',
        new Date(2017, 7, 3, 8, 47), '已经按要求进行调整，请从新进行审批。'),
      new Schedule('circlecheck', '#259b24', '#259b24', '莫桂富 （乙方现场工程师）', '已同意', new Date(2017, 7, 3, 9, 47), '情况属实'),
      new Schedule('circlecheck', '#259b24', '#259b24', '杜伦 （工程部经理）', '已同意', new Date(2017, 7, 4, 10, 47), '情况属实'),
      new Schedule('circlecheck', '#259b24', '#259b24', '李杰 （项目副总经理）', '已同意', new Date(2017, 7, 4, 17, 47), '情况属实'),
      new Schedule('circlecheck', '#259b24', '#259b24', '况小伟 （监理总监）', '已同意', new Date(2017, 7, 4, 18, 47), '情况属实'),
      new Schedule('circlecheck', '#259b24', '#259b24', '陈聪 （项目成本负责人）', '已同意', new Date(2017, 7, 10, 19, 47), '情况属实')
    ];


    const workDetailForms: FormBase<any>[] = [
      new RelationForm({key: 'contract', label: '对应合同', isForm: false,
        title: '武汉尚都花园总承包工程施工合同',
        content: '【武汉-尚都花园-一期】【总包】【1】【2017】', order: 1}),
      new RelationForm({top: 'top', key: 'finished', label: '对应完工确认', isForm: false,
        title: '武汉尚都花园总承包工程施工合同',
        content: '【武汉-尚都花园-一期】【总包】【1】【2017】', order: 2}),
      new LinkForm({top: 'top', key: 'onsite', isForm: false,
        label: title, icon: 'onsite', color: '#d4237a', fontColor: '#101010', order: 3}),
      new LabelForm({key: 'number', label: '变更编号', isForm: false, value: content, order: 4}),
      new LabelForm({key: 'content', label: '详细内容', isForm: false, approved: true, value: detail, order: 5}),
      new LabelForm({key: 'type', label: '变更类型', isForm: false, value: '现场签证', order: 6}),
      new LabelForm({key: 'reason', label: '变更原因', isForm: false, value: '现场施工：现场环境及地质影响', order: 7}),
      new LabelForm({key: 'propose', label: '提出阶段', isForm: false, value: '工程实施前', order: 8}),
      new LabelForm({key: 'profession', label: '涉及专业', isForm: false, value: '土建', order: 9}),
      new LabelForm({key: 'money', label: '变更申报金额', isForm: false, value: '44,494.31 元', order: 10}),
      new PhotosForm({top: 'top', key: 'photos', isForm: false, imgUrls: [
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1275/12759630/raw_1505548320.jpeg'], order: 11}),
      new ScheduleForm({top: 'top', key: 'schedule', isForm: false, label: '项目现场确认人', name: '审批人',
        icon: 'users', color: '#009688', fontColor: '#259b24', schedules: schedules, order: 12}),
      new AvatarsForm({key: 'cc', label: '抄送人', avatars: [ new Avatar('avatar-5', '张涮锅'), new Avatar('avatar-8', '李逵德')],
        isForm: false, order: 13})
    ];
    return workDetailForms;
  }

  getWorkForms() {
    const contractTitles: string[] = ['武汉尚都花园总承包工程施工合同', '深圳尚都花园总承包工程施工合同',
      '上海尚都花园总承包工程施工合同', '广州尚都花园总承包工程施工合同', '北京尚都花园总承包工程施工合同'
    ];
    const workForms: FormBase<any>[] = [
      new RelationForm({key: 'contract', label: '对应合同', required: true, titles: contractTitles,  title: '点击选择对应合同', order: 1}),
      new InputForm({top: 'top', key: 'overview', label: '变更概述', required: true, placeholder: '必填，20字内', order: 2}),
      new TextareaForm({key: 'details', label: '详细内容', required: true, placeholder: '必填，300字内', order: 3}),
      new LabelForm({top: 'top', key: 'type', label: '变更类型', required: false, value: '现场签证', order: 4}),
      new InputForm({key: 'reason', label: '变更原因', required: true, placeholder: '必选', order: 5}),
      new InputForm({key: 'propose', label: '提出阶段', required: true, placeholder: '必选', order: 6}),
      new InputForm({key: 'profession', label: '涉及专业', required: false, placeholder: '非必选', order: 7}),
      new InputForm({key: 'money', label: '变更申报金额(元)', required: false, placeholder: '非必填', order: 8}),
      new PhotosForm({top: 'top', key: 'photos', required: false, imgUrls: [
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1276/12760397/raw_1505550329.jpeg',
        'https://modao.cc/uploads3/images/1275/12759630/raw_1505548320.jpeg'], order: 9}),
      new LinkForm({top: 'top', key: 'link', required: false, url: '', label: '添加附件', icon: 'link', order: 10}),
      new LinkForm({top: 'top', key: 'plus', required: false, url: '/work/add/inventory', value: this.inventoryFormsValue,
        label: '添加工作量清单', icon: 'add', order: 11}),
      new LinkForm({top: 'top', key: 'users', required: false,
        label: '项目现场确认人', icon: 'users', color: '#009688', fontColor: '#259b24', order: 12}),
      new AvatarsForm({key: 'approval', label: '审批人', required: false,
        avatars: [ new Avatar('avatar-5', '张涮锅'), new Avatar('avatar-8', '李逵德')], order: 13}),
      new AvatarsForm({key: 'cc', label: '抄送人', required: false, order: 14})
    ];
    return workForms;
  }

  onShowBySrv(workForm: FormBase<any>, callback: Function) {
    if (!workForm.isForm) {
      return;
    }
    const data: string[] = workForm['titles'] as string[];
    const index: number = Math.floor(data.length / 2) as number;
    const value: string = data[index];
    console.log(index);
    const options: PickerOptions = {cancel: '取消', confirm: '确定', backdrop: true};
    this.srv.show(data, value, [], options).subscribe((res: any) => {
      this.contractTitles.forEach(element => {
        if (element.title === res.value) {
          callback(element);
          return;
        }
      });
    });
  }

}



