import { Component, Input, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';

import { FormBase } from '../../../../class/form-base';
import { FormControlService } from '../../../../services/form-control/form-control.service';

@Component({
  selector: 'app-work-form',
  templateUrl: './work-form.component.html',
  styleUrls: ['./work-form.component.css'],
  providers: [ FormControlService ]
})

export class WorkFormComponent implements OnInit {
  @Input() forms: FormBase<any>[];
  form: FormGroup;
  payLoad = '';
  isWeb = true;

  constructor(private fcs: FormControlService) {}

  ngOnInit() {
    this.isWeb = dd.version === null ;
    this.form = this.fcs.toFormGroup(this.forms);
  }

  onSubmit() {
    this.payLoad = JSON.stringify(this.form.value);
  }
}




