import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { SettingService } from '../../../../services/setting/setting.service';
import { DingtalkService } from '../../../../services/dingtalk/dingtalk.service';
// import { LoggerService } from '../../../../services/logger/logger.service';
import { MenuItem } from '../../../../class/menu-item';
import { WorkService } from '../../../../services/work/work.service';
import { OnsiteItem, OnsiteItemStatus, OnsiteStatus } from '../../../../class/onsite-item';

@Component({
  selector: 'app-page-work-list',
  templateUrl: './page-work-list.component.html',
  styleUrls: ['./page-work-list.component.css']
})
export class PageWorkListComponent implements OnInit, OnDestroy {
  top = '0';
  onsiteList: OnsiteItem[] = [];
  allOnsiteList: OnsiteItem[] = [];
  statusList = OnsiteStatus.status;
  selectedStatus = OnsiteStatus.status[0];
  type = '';

  constructor(private settings: SettingService,
    private dingtalk: DingtalkService,
    // private logger: LoggerService,
    private router: Router,
    private work: WorkService,
    private route: ActivatedRoute) {
    const that = this;
    this.top = dd.version === null ? '4.66rem' : '0';
    const items = [
      new MenuItem('1', '搜索', 'search')
    ];
    this.dingtalk.setNavigationMenu(items, data => {
    });
    this.route.paramMap.subscribe((params: ParamMap) => {
      that.type = params.get('type');
    });
    work.getOnsiteList(list => {
      this.allOnsiteList = this.onsiteList = list;
    });
  }

  goAddWork() {
    this.settings.isBack = false;
    this.settings.setTitle('新增现场签证申报');
    this.router.navigate(['work', 'add', 'onsite', this.type]);
  }

  setSelectedStatus(status: OnsiteItemStatus) {
    if (this.selectedStatus === status) {
      return;
    }
    this.selectedStatus = status;
    if (status.id === 0) {
      this.onsiteList = this.allOnsiteList;
      return;
    }
    const tlist: OnsiteItem[] = [];
    this.allOnsiteList.forEach(element => {
      if (status.content === element.status) {
        tlist.push(element);
      }
    });
    this.onsiteList = tlist;
  }

  onClick(onsite: OnsiteItem) {
    this.settings.isBack = false;
    this.settings.setTitle('查看现场签证申报');
    this.router.navigate(['work', 'onsite', 'detail', onsite.id]);
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    this.dingtalk.setNavigationRight();
  }

}



