import { Component, OnInit } from '@angular/core';
import { MenuTab } from '../../../../class/menu-base';
import { DingtalkService } from '../../../../services/dingtalk/dingtalk.service';
import { GlobalConst } from '../../../../class/global-const';

@Component({
  selector: 'app-page-setting',
  templateUrl: './page-setting.component.html',
  styleUrls: ['./page-setting.component.css']
})
export class PageSettingComponent implements OnInit {
  list: MenuTab[];

  constructor(private dingtalk: DingtalkService) {
    this.list = GlobalConst.settingMenuList;
  }

  ngOnInit() {
  }

  onClick(item: MenuTab) {
    switch (item.title) {
      case '清除缓存':
        this.clearCache();
        break;
      default:
        break;
    }
  }

  /**
   * 清除缓存
   */
  clearCache() {
    const that = this;
    this.dingtalk.showPreloader('清除缓存中...', true, res => {
      setTimeout(function() {
        that.dingtalk.hidePreloader(() => {
          that.dingtalk.toast('清理完成');
        });
      }, 1500);
    });
  }

}



