export class FormBase<T> {
    value: T;
    top: string;
    key: string;
    label: string;
    required: boolean;
    order: number;
    isForm: boolean;
    controlType: string;

    constructor(options: FormBaseOpt<T> = {}) {
      this.value = options.value;
      this.top = options.top || '';
      this.key = options.key || '';
      this.label = options.label || '';
      this.required = !!options.required;
      this.isForm = !!options.isForm;
      this.order = options.order === undefined ? 1 : options.order;
      this.controlType = options.controlType || '';
    }
}
interface FormBaseOpt<T> {
    value?: T;
    key?: string;
    top?: string;
    label?: string;
    required?: boolean;
    isForm?: boolean;
    order?: number;
    controlType?: string;
}
export class InputForm extends FormBase<string> {
    controlType = 'input';
    placeholder: string;
    type: string;
    input: string;

    constructor(options: InputFormOpt = {}) {
      super(options);
      this.value = options.value || '';
      this.type = options.type || 'text';
      this.input = options.input || 'text';
      this.placeholder = options.placeholder || '';
    }
}
interface InputFormOpt extends FormBaseOpt<string> {
    placeholder?: string;
    type?: string;
    input?: string;
}
export class TextareaForm extends FormBase<string> {
    controlType = 'textarea';
    placeholder: string;
    rows: string;

    constructor(options: TextareaFormOpt = {}) {
      super(options);
      this.value = options.value || '';
      this.rows = options.rows || '3';
      this.placeholder = options.placeholder || '';
    }
}
interface TextareaFormOpt extends FormBaseOpt<string> {
    placeholder?: string;
    rows?: string;
}
export class RelationForm extends FormBase<Relation> {
    controlType = 'relation';
    id: string;
    titles: string[];
    title: string;
    content: string;

    constructor(options: RelationFormOpt = {}) {
        super(options);
        this.value = options.value || new Relation();
        this.id = options.id || '';
        this.title = options.title || '';
        this.titles = options.titles || [];
        this.content = options.content || '';
    }
}
interface RelationFormOpt extends FormBaseOpt<Relation> {
    id?: string;
    title?: string;
    titles?: string[];
    content?: string;
}
export class Relation {
    id: number;
    title: string;
    content: string;
    constructor(
        id?: number,
        title?: string,
        content?: string,
    ) {
        this.id = id || 0;
        this.title = title || '';
        this.content = content || '';
    }
}
export class LinkForm extends FormBase<any[]> {
    controlType = 'link';
    private icon: string;
    color: string;
    fontColor: string;
    url: string;

    constructor(options: LinkFormOpt = {}) {
        super(options);
        this.value = options.value || [];
        this.icon = options.icon || '';
        this.url = options.url || '';
        this.color = options.color || '#0076FF';
        this.fontColor = options.fontColor || '#0076FF';
    }

    get mbClass () {
        return '#yjicon-' + this.icon;
    }
}
interface LinkFormOpt extends FormBaseOpt<any[]> {
    icon?: string;
    color?: string;
    fontColor?: string;
    url?: string;
}
export class PhotosForm extends FormBase<string[]> {
    controlType = 'photos';
    private imgUrls: string[];

    constructor(options: PhotosFormOpt = {}) {
        super(options);
        this.value = options.value || [];
        this.imgUrls = options.imgUrls || [];
    }
}
interface PhotosFormOpt extends FormBaseOpt<string[]> {
    imgUrls?: string[];
}
export class LabelForm extends FormBase<string> {
    controlType = 'label';
    approved: boolean;

    constructor(options: LabelFormOpt = {}) {
        super(options);
        this.value = options.value || '';
        this.approved = !!options.approved;
    }

    get toHtml() {
        return this.value.replace(/\s/g, '').split('||');
    }
}
interface LabelFormOpt extends FormBaseOpt<string> {
    approved?: boolean;
}
export class AvatarsForm extends FormBase<string> {
    controlType = 'avatars';
    avatars: Avatar[] = [];

    constructor(options: AvatarsFormOpt = {}) {
        super(options);
        this.value = options.value || '';
        this.avatars = options.avatars || [];
    }
}
interface AvatarsFormOpt extends FormBaseOpt<string> {
    avatars?: Avatar[];
}
export class Avatar {
    constructor(
        public avatar: string,
        public name: string
    ) {}

    get mbClass () {
        return '#yjicon-' + this.avatar;
    }
}

export class ScheduleForm extends FormBase<Schedule[]> {
    controlType = 'schedule';
    name: string;
    icon: string;
    color: string;
    fontColor: string;
    schedules: Schedule[];

    constructor(options: ScheduleFormOpt = {}) {
        super(options);
        this.value = options.value || [];
        this.name = options.name || '';
        this.icon = options.icon || '';
        this.color = options.color || '';
        this.fontColor = options.fontColor || '';
        this.schedules = options.schedules || [];
    }

    get mbClass () {
        return '#yjicon-' + this.icon;
    }
}
interface ScheduleFormOpt extends FormBaseOpt<Schedule[]> {
    name?: string;
    icon?: string;
    color?: string;
    fontColor?: string;
    schedules?: Schedule[];
}

export class Schedule {
    constructor(
        public icon: string,
        public color: string,
        public statusColor: string,
        public name: string,
        public status: string,
        public date: Date,
        public content: String = ''
    ) {}

    get mbClass () {
        return '#yjicon-' + this.icon;
    }

    get datestr () {
        return this.date.getFullYear() + '-' + (this.date.getMonth() + 1) + '-' +
            this.date.getDate() + ' ' + this.date.getHours() + ':' + this.date.getMinutes();
    }
}







