import { Injectable } from '@angular/core';
// import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { SmsNotice } from '../../class/menu-base';
import { GlobalConst } from '../../class/global-const';

@Injectable()
export class SmsNoticeService {

  constructor(private http: HttpClient) {
  }
  /**
   * 获取通知事项列表
   */
  getList(callback: Function) {
    this.http.get<SmsNotice[]>(GlobalConst.httpUrls.smsNoticet)
      .subscribe(data => {
        const tlist: SmsNotice[] = [];
        data.forEach(element => {
          tlist.push(new SmsNotice({title: element['title'], content: element['content'], icon: 'file', color: 'red'}));
        });
        callback(tlist);
      });
  }

}



