import { Injectable } from '@angular/core';
import { User } from '../../class/user';
import { DingtalkService } from '../dingtalk/dingtalk.service';
import { LoggerService } from '../logger/logger.service';

@Injectable()
export class UserService {
  private currentUser: User;

  constructor(private dingtalk: DingtalkService,
    private logge: LoggerService) {
    this.currentUser = new User('0', '敖 博', 'avatar-1', '', '138123400981');
  }

  /**
   * 获取当前登录用户信息
   */
  getUser(callback?: Function) {
    if (callback !== undefined || callback !== null) {
      this.dingtalk.getUser( info => {
        this.currentUser.avatar = info['avatar'];
        this.currentUser.nickName = info['nickName'];
        callback(this.currentUser);
      });
    }
    return this.currentUser;
  }

}



