import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ToptipsService } from 'ngx-weui/toptips';
import { FormBase } from '../../../../../../class/form-base';
import { SettingService } from '../../../../../../services/setting/setting.service';
import { DingtalkService } from '../../../../../../services/dingtalk/dingtalk.service';
import { WorkService } from '../../../../../../services/work/work.service';
import { LoggerService } from '../../../../../../services/logger/logger.service';
import { FormControlService } from '../../../../../../services/form-control/form-control.service';

@Component({
  selector: 'app-page-inventory',
  templateUrl: './page-inventory.component.html',
  styleUrls: ['./page-inventory.component.css']
})
export class PageInventoryComponent implements OnInit, OnDestroy {
  isWeb = true;
  valid = false;
  object: {forms: FormBase<any>[], form: FormGroup, name: string, active: boolean};
  list: {forms: FormBase<any>[], form: FormGroup, name: string, active: boolean}[];
  constructor(private dingtalk: DingtalkService,
    private work: WorkService,
    private logger: LoggerService,
    private fcs: FormControlService,
    private srv: ToptipsService,
    private settings: SettingService) {
      this.isWeb = dd.version === null ;
      const forms:  FormBase<any>[] = work.getInventoryForms();
      const form: FormGroup = this.fcs.toFormGroup(forms);
      this.object = {forms: forms, form: form, name: '工作量清单' + 1, active: true};
      this.list = [this.object];
    }

  ngOnInit() {
  }

  ngOnDestroy() {
  }

  plusInventory() {
    const index = this.list.length + 1;
    const object = {forms: this.object.forms, form: this.object.form, name: '工作量清单' + index, active: false};
    this.list.push(object);
  }
  onSubmit(form: FormGroup) {
    this.logger.log(JSON.stringify(form.value));
  }

  select(index: number) {
    this.logger.log(index);
  }

  Submit() {
    const tlist = this.getList();
    if (this.valid) {
      this.work.inventoryFormsValue = tlist;
      history.back();
    }else {
      this.srv.warn('请按提示填写必填信息');
    }
  }

  getList(): any[] {
    let count = 0;
    const tlist = [];
    this.list.forEach(element => {
      tlist.push(element.form.value);
      if (element.form.valid) {
        count++;
      }
    });
    this.valid = count === tlist.length;
    return tlist;
  }

  showPreloader() {
    const that = this;
    that.dingtalk.showPreloader('保存中...', true, res => {
      setTimeout(() => {
        that.dingtalk.hidePreloader(results => {
          that.dingtalk.toast('保存成功并返回', 'success', () => {
            that.dingtalk.goBack();
          });
        });
      }, 1000);
    });
  }

}



